(function() {
  'use strict';

  angular
    .module('awkwardAnnie')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($log) {
    var vm = this;
    vm.animationTitle = "";
    $log.log(vm.animationTitle);
    vm.playerScore = 0; // Main controller controls score, what characters were spoken too
    vm.convosWithMike = 0;
    vm.roomKey = "breakRoom"; // Look at gameManagerData for correct names
    vm.hideLinearDialogue = true;
    // for design purposes this is set to false, switch back to true when done
    vm.hideBranchingDialogue = false;
  }
})();

(function() {
  'use strict';

  angular
    .module('awkwardAnnie')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('awkwardAnnie')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    //remove url later, before release
    // use different controller for welcome screen
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });
    $stateProvider
      .state('RoomEngine', {
        url: '/RoomEngine',
        templateUrl: 'app/components/gameManager/awkwardAnnieGame.html',
        controller: 'MainController',
        controllerAs: 'main'
      });
    $stateProvider
      .state('DialogueEngine', {
        url: '/DialogueEngine',
        templateUrl: 'app/components/dialogueManager/dialogueManager.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

